.products-gallery {

}

.products-gallery--goods {
  padding-left: 6.8vw;
  margin-bottom: 3.75vw;

  @media (max-width: 992px) {
    margin-bottom: 6vw;
  }
}

.products-gallery__list {
  padding-top: 2.24vw;
}

.products-gallery__left {
  padding-left: 10.4vw;
}

.products-gallery__right {
  padding-right: 10.4vw;
}

.products-gallery__item {
  margin-bottom: 95px;
}

.products-gallery--idea h2 {
  padding-left: 10.4vw;
}

.products-gallery--goods h2 {
  @media (max-width: 992px) {
    text-align: center;
  }
}

.products-gallery p {
  font-family: inherit;
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400,"wdth" 90;
  margin: 0;
  padding-top: 2.08vw;
  padding-bottom: 3.59vw;
}

.products-gallery__item {

}

.products-gallery .gallery-card__item {
  max-width: 346px;
  text-decoration: none;
  margin: 0 auto;
}

.products-gallery .info-card {
  margin-top: 1.7vw;
}

.products-gallery .info-card p {
  font-size: clamp(20px, 1.4vw, 25px);
  line-height: 24px;
  padding: 0;
  margin-top: 1.25vw;
}

.products-gallery .gallery-card__item {

}

.products-gallery__img img {
  height: 887px;
  object-fit: cover;
}

.products-gallery__direct {
  width: 100%;
  display: flex;
  justify-content: space-between;

@media (max-width: 1080px) {
    width: 432px;
    margin: 0 auto;
  }

@media (max-width: 767px) {
    width: 50%;
    margin: 0 auto;
  }
}

.products-gallery__direct li {
  display: flex;
  justify-content: space-between;
}

.products-gallery__item {
  display: flex;
  align-items: center;
  color: var(--back-main);
  text-decoration: none;
  margin-bottom: 0;
}

.products-gallery__direct p {
  font-size: clamp(20px, 1.4vw, 25px);
  padding: 0;
}

.products-gallery__direct img {
  width: 13px;
  height: 21px;
  margin-right: 32px;
}

.products-gallery__direct li:nth-child(2) img {
  margin-right: 0;
  margin-left: 32px;
  transform: rotate(180deg);

@media (max-width: 767px) {
  }
}

@media (max-width: 1080px) {
    .products-gallery__direct p {
      font-size: 0;
  }
}
