.field-delivery__list {
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(4, max-content);
  column-gap: 2.7vw;
  row-gap: 25px;
  justify-content: space-between;
  margin-top: 25px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
}

.field-delivery__item {
  display: grid;
}

.field-delivery__item label {
  position: relative;
  font-size: clamp(18px, 1.3vw, 23px);
  padding-left: 50px;

  @media (max-width: 1080px) {
    font-size: 23px;
  }
}

.field-delivery__item input[type="radio"] + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background: transparent;
  border: 1px solid var(--back-input);
  border-radius: 50%;
}

.field-delivery__item input[type="radio"]:checked + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  border: 1px solid var(--back-input);
  background: url("../img/marker-circle-nav.png");
  background-repeat: no-repeat;
  background-size: 25px 25px;
  background-position: 50% 50%;
}

.field-delivery__item a {
  color: var(--back-main);
  text-decoration: underline;

  @media (max-width: 580px) {
    display: block;
  }
}
