.gallery-top {
  margin-top: 20px;
}

.gallery-top.swiper-container {
  position: relative;
  height: 50.4vw;
  overflow: hidden;
  margin: 0 auto;

  outline: 1px solid green;


  @media (max-width: 1280px) {

  }

  @media (max-width: 992px) {

  }
}

.gallery-top::after {
  position: absolute;
  content: "";
  width: 110px;
  height: 110px;
  background-image: url("../img/label-logo.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: 20;
  right: 29px;
  bottom: 26px;
}

.gallery-top .swiper-wrapper {
  width: auto;
  height: 100%;
}

.gallery-top.swiper-slide {
  width: 100%;
  height: 50.4vw;
}

.gallery-top__item.swiper-slide {
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
  overflow: hidden;
}


.gallery-top.swiper-container-autoheight {
  &, .swiper-slide {
    width: auto;
    height: 50.4vw;

  @media (max-width: 992px) {

    }

  @media (max-width: 680px) {

    }

  @media (max-width: 480px) {

    }
  }
}

.gallery-top.swiper-container-autoheight {
  &, .swiper-slide img {
    display: block;
    width: auto;
    height: 100%;
  }
}
