.product-order {

}

.product-order__count {

}

.product-order__count input[type="number"] {
  display: inline-block;
  width: 67px;
  height: 33px;
  font-size: clamp(20px, 1.4vw, 25px);
  font-variation-settings: "wght" 700, "wdth" 90;
  color: var(--basic-green);
  text-align: center;
  border: 1px solid var(--back-main);
  padding-left: 10px;

}

.product-order__count p {
  margin-top: 8px;
}

.product-order__count .info-card__desc {
  margin-top: 23px;
}

.product-order article {
  position: relative;
}

.product-order__btn {
  position: absolute;
  width: 35px;
  height: 35px;
  top: -1.5vw;
  right: -1.04vw;
  background: url("../img/btn-close.png") transparent;
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;

  @media (max-width: 1280px) {
    background-size: 75% auto;
  }

  @media (max-width: 992px) {
    background-size: 80% auto;
  }

  @media (max-width: 680px) {
    right: 1.04vw;
  }
}

.product-order__btn span {
  visibility: hidden;
}

.product-order__sum {
  padding-left: 6.8vw;
  margin-top: 5.3vw;
  margin-bottom: 5.3vw;

  @media (max-width: 992px) {
    padding-left: 0;
  }
}

.product-order h3 > span {
  color: var(--basic-dark-pink);
  font-variation-settings: "wght" 300, "wdth" 90;
}

.product-order h3 {
  @media (max-width: 767px) {
    text-align: center;
  }

  @media (max-width: 580px) {
    font-size: clamp(32px, 9vw, 38px);
    line-height: 42px;
  }
}
